import {Tag, ValuePriority} from '../tag';


export class FlexAlign extends Tag {
  readonly tag = 'flexAlign';

  build(input?: string): Map<string, ValuePriority> {
    input = input || 'stretch';
    const cache = this.getCache(input);

    if (cache) {
      return cache;
    }

    const styles: Map<string, ValuePriority> = new Map();

    // Cross-axis
    const key = 'align-self';
    switch (input) {
      case 'start':
        styles.set(key, {value: 'flex-start', priority: 0});
        break;
      case 'end':
        styles.set(key, {value: 'flex-end', priority: 0});
        break;
      default:
        styles.set(key, {value: input, priority: 0});
        break;
    }

    this.setCache(input, styles);

    return styles;
  }
}
